.loader-overlay {
    height: 60vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
 }
.loader {
    display: inline-block;
    width: 50px;
    height: 50px;
    border: 3px solid rgba(195, 195, 195, 0.6);
    border-radius: 50%;
    border-top-color: #636767;
    -webkit-animation: load 1s ease-in-out infinite;
            animation: load 1s ease-in-out infinite;
}
@keyframes load {
    to {
      -webkit-transform: rotate(360deg);
    }
  }
  @-webkit-keyframes load {
    to {
      -webkit-transform: rotate(360deg);
    }
  }
nav {
    background-color: rgba(51,51,51,.75) !important;
    box-shadow: 0 2px 2px var(--main-red);
    padding: 1rem 1.5rem !important;
}
.navbar {
    display: flex !important;
    justify-content: center !important;
    align-items: center;
}
nav .navbar-brand {
    margin: 0; padding: 0;
}
nav .navbar-brand img {
    width: 5rem; height: 5rem;
    transition: all .3s linear;
    position: absolute; left: 0; top: .2rem;
    margin-left: 2rem;
}
nav .navbar-brand img:hover {
    opacity: .5;
}
nav button.btn {
    border: none;
}
nav button.btn:hover {
    background: none !important;
}
nav svg {
    font-size: 3rem;
    color: var(--white);
    transition: all .3s linear;
}
nav svg:hover {
    color: var(--main-red);
}

nav form {
    display: flex; justify-content: center; align-items: center;
}
nav form input[type="search"] {
    border-radius: 0 !important;
    border: none !important; 
    background: none !important;
    color: var(--white) !important; font-weight: 500;
    font-size: 1.75rem;
    padding: .25rem 1rem;
    border-bottom: 2px solid var(--white) !important;
    width: 20rem;
    transition: all .5s ease;
}
nav form input[type="search"]:focus {
    width: 40rem; 
    box-shadow: none !important; 
}
nav form input::-webkit-input-placeholder {
    color: var(--white) !important;
    font-weight: 500;
}
nav form input:-ms-input-placeholder {
    color: var(--white) !important;
    font-weight: 500;
}
nav form input::-ms-input-placeholder {
    color: var(--white) !important;
    font-weight: 500;
}
nav form input::placeholder {
    color: var(--white) !important;
    font-weight: 500;
}
nav #customElements {
    position: absolute; top: 1.2rem; right: 2rem;
    display: flex; justify-content: center;
    margin: 0;
}
nav #customElements li {
    display: flex; justify-content: center; align-items: center;
    padding: .5rem; 
}
nav #customElements li:first-child {
    margin-right: 2rem;
}



/* MEDIA QUERIES */
/* Extra small  */
@media (max-width: 575.98px) { 
    nav {
        width: 100vw;
    }
    nav form input[type="search"] {
        width: 10rem;
    }
    nav form input[type="search"]:focus {
        width: 20rem; 
    }
    nav #customElements li:first-child {
        margin-right: 0;
    }
}

